import React from "react";
import g from "glamorous";
import { withPrefix } from "gatsby-link"

import Jumbotron from "../components/jumbotron";
import Navbar from "../components/navbar";

export default ({ data }) => {
  console.log('page', data);
  const { team, navbar } = data.site.siteMetadata;

  return (
    <div className="team">
      <Navbar data={data} active={navbar.nav3} fixed />
      <div className="jumbotron">
        <div className="container">
          <Jumbotron caption={team.jumbotron.caption} des={team.jumbotron.des} />
        </div>
      </div>
      <div className="section section-management">
        <div className="container">
          <h2 className="h2">{team.section1.header}</h2>
          {team.section1.members.map((member, i) => {

            return (
              <div className="detail" key={i}>
                <div className="image-container">
                  <div className="image image-<%= members[i].name.split(' ')[0].toLowerCase() %>">
                    <img src="img/member/member-m-jon.jpg" />
                  </div>
                </div>
                <div className="intro">
                  <h3 className="h3">{member.name.toUpperCase()}</h3>
                  <span className="title">{member.title.toUpperCase()}</span>
                  <hr className="hr" />
                  <div className="p2">{member.intro}</div>
                </div>
              </div>
            );
          })}

          <div className="members">
            {team.section1.members.map((member) => {
              <div className="item">
                <div className="image image-<%= members[i].name.split(' ')[0].toLowerCase() %>">
                  <img src="img/member/member-s-jon.jpg" />
                </div>
                <h3 className="h3">{member.name.toUpperCase()}</h3>
              </div>
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export const query = graphql`
  query TeamQuery {
    site {
      siteMetadata {
        navbar {
          navs
          nav1
          nav2
          nav3
          nav4
          href1
          href2
          href3
          href4
        }
        team {
          jumbotron {
            caption
            des
          }
          section1 {
            header
            members {
              name
              title
              intro
            }
          }
          section2 {
            header
          }
        }
      }
    }
  }
 `


